.item {
  /* stylelint-disable-next-line max-line-length */
  composes: item from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}

.alternateItem {
  /* stylelint-disable-next-line max-line-length */
  composes: alternateItem from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}

.underlay {
  /* stylelint-disable-next-line max-line-length */
  composes: underlay from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}

.overlay {
  /* stylelint-disable-next-line max-line-length */
  composes: overlay from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}

.posterContainer {
  composes: posterContainer from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: calc(
    (($downloadCellHeight - 2 * $summaryCellPadding) * (16 / 9)) +
      $summaryCellPadding
  );
}

.summaryContainer {
  composes: summaryContainer from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';

  display: flex;
  justify-content: center;
  flex-direction: column;
}

.titleContainer {
  composes: titleContainer from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';

  font-size: inherit;
}

.errorIcon {
  margin-right: 5px;
  color: $dangerColor;
}

.title {
  composes: title from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}

.statusContainer {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  margin-right: 20px;
}

.statusText {
  margin-right: 15px;
  color: $defaultLinkColor;
}

.actions {
  composes: actions from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';

  display: flex;
  align-items: center;
  padding: 0 10px;
}

.summary {
  composes: summary from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';

  margin-top: 0;
  max-height: calc($lineHeightComputed * $defaultFontSize * 2);
  height: auto;
  color: $dimColor;
}

.button {
  composes: button from '~desktop/common/js/views/v3/components/metadata/lists/MetadataSummaryCell.css';
}
